import React from 'react';
import dummy from '../../data/products.json';
import {BsCart2} from "react-icons/bs"

function Bestitem(props) {
  return (
    <>
      <article className='best'>
        <h2>뷰컬페에서 가장 인기있어요🏅 &#8250;</h2>
        <p className='detail'>지금 뷰티컬리는 최대 85% 빅세일 중!</p>

        {/* map함수를 활용하여 배열데이터 출력 */}
        <ul>
          {dummy.products.map(products =>(
            <li key={products.filename}>
              <img src={`${process.env.PUBLIC_URL}/images/${products.filename}`} alt="" />
              <span className='cart'><BsCart2 />담기</span>
              <p>{products.name}</p>
              <p>{products.price}</p>
              <p><span>{products.sale}</span>{products.price2}</p>
            </li>
          ))}
        </ul>
      </article>
    </>
  );
}

export default Bestitem;